<template>
    <div>
    <ul>
      <li v-for="(movie,index) in movies" :key="index" :class="{active:index===currentIndex}" @click="changeColor(index)" >{{index+"---"+movie}}</li>
    </ul>
  </div>
</template>
<script>
export default {
    data(){
        return{
            currentIndex: 0,
            movies: ["海王","海贼王","火影忍者","复仇者联盟"]
        } 
      },
      methods: {
        changeColor(index){
          this.currentIndex = index
        }
      }
}
</script>
<style scoped>
    .active{
      color:red;
    }
</style>