<template>
    <div id="main">
        <div id="show">
            <div id="msg">
                <div id="title">
                   <span>
                       房间4901
                    </span> 
                </div>
                <Scroll :on-reach-top="handleReachTop" height = "464">
                    <div :class="{singlemsg:true}" v-for="(msg,index) in msgs" :key="index" @click="check(index)">
                        <div id="headphoto">
                            <img :src="msg.head" alt="">
                        </div>
                        <div id="content">
                            <div id="info">
                                {{msg.name}}{{msg.time}}
                            </div>
                            <div id="message">
                                {{msg.data}}
                            </div>
                        </div>
                    </div>
                </Scroll>
            </div>
            <div id="footer">
                <Modal closable="false" footer-hide="false" width="420" v-model="modal3">
                    <picker id="emoji" @select="addEmoji" />
                </Modal>
                <Input v-model="texts" placeholder="0^0" style="width: 600px" />
                <Icon @click="modal3 = true" id="face" type="md-happy" size="25" />
                <Icon id="send" type="md-arrow-round-forward" size="25" @click="sendmsg" />
            </div>
        </div>
    </div>
</template>

<script>
    import {
        Picker
    } from 'emoji-mart-vue'
    
    var ws = new WebSocket('ws://localhost:2333')
    var headpath = null
    var nikename = null
    var da = new Date()
    var t = da.toLocaleTimeString()
    var head = [
        'https://s2.ax1x.com/2020/02/02/1JTsJJ.jpg',
        'https://s2.ax1x.com/2020/02/02/1JTyW9.jpg',
        'https://s2.ax1x.com/2020/02/02/1YSEiq.jpg',
        'https://s2.ax1x.com/2020/02/02/1YSezT.jpg',
        'https://s2.ax1x.com/2020/02/02/1YPcNj.jpg',
        'https://s2.ax1x.com/2020/02/02/1YPg4s.jpg',
        'https://s2.ax1x.com/2020/02/02/1YPsHg.jpg',
        'https://s2.ax1x.com/2020/02/02/1YP6EQ.gif',
        'https://s2.ax1x.com/2020/02/02/1YPDu8.jpg',
        'https://s2.ax1x.com/2020/02/02/1YPRCn.jpg',
        'https://s2.ax1x.com/2020/02/02/1YPfg0.jpg',
        'https://s2.ax1x.com/2020/02/02/1YPhvV.gif',
    ]
    var namelist = ["皮卡丘","杰尼龟","可达鸭","果然翁","小火龙","喵喵","呆呆兽","热带龙"]
    var index = Math.floor((Math.random()*namelist.length))
    if(nikename == null){
        nikename = namelist[index]
        headpath = head[index]
    }
    var flag = 0;
    setTimeout(function(){
        if(flag === 0){
        ws.send(JSON.stringify({
            name: nikename,
            time: t,
            data: "我已经进来啦~",
            head: headpath,
            type:"tip"
        }))
        flag = 1;
        }
    },2000)
    
    export default {
        data() {
            return {
                aaa: false,
                modal3: false,
                texts: "",
                msgs: [ ]
            }
        },
        methods: {
            showemoji() {
                this.aaa = !(this.aaa)
            },
            addEmoji(emoji) {
                this.texts += emoji.native
            },
            check(index) {

            },
            handleReachTop() {
                return new Promise(resolve => {
                    setTimeout(() => {
                        const first = this.list2[0];
                        for (let i = 1; i < 11; i++) {
                            this.list2.unshift(first - i);
                        }
                        resolve();
                    }, 2000);
                });
            },
            sendmsg(){
                ws.send(JSON.stringify({
                    name: nikename,
                    time: t,
                    data: this.texts,
                    head: headpath,
                    type:"m"
                }))
                this.texts = ''
            }
        },
        components: {
            Picker
        },
        created(){
            ws.onmessage = e => {
                var mm = JSON.parse(e.data)
                console.log(mm)
                this.msgs.push(mm)
                console.log(this.msgs)
            }
            
        }
    }
</script>
<style scoped>
    #main {
        background-image: url('../assets/b2.jpg');
        height: 635px;
        position: relative;
    }

    #show {
        position: relative;
        display: block;
        padding: 35px 330px;
    }

    #msg {
        width: 680px;
        height: 520px;
        border:2px solid rgb(16, 196, 172);
        background-color: rgba(42, 140, 165, 0.87);
    }
    #title{
        height: 50px;
        background-color: rgb(236, 236, 219);
    }
    #title span{
        font-family: '楷体';
        text-align: center;
        position: relative;
        display: block;
        font-size: 20px;
        top: 10px;
    }
    .singlemsg {
        width: 100%;
        height: 100px;
        text-align: center;
        display: inline-flex;
    }

    #footer {
        display: inline-flex;
        position: relative;
        /* top:50px; */
    }

    #emotion {
        display: block;
        margin: 0 auto;
    }

    #face {
        cursor: pointer;
        position: relative;
        top: 5px;
        left: 20px;
    }

    #send {
        cursor: pointer;
        position: relative;
        top: 5px;
        left: 30px;
    }

    #emoji {
        z-index: 1000;
    }

    #content {
        height: 100%;
        width: auto;
    }

    #headphoto img {
        position: relative;
        display: block;
        width: 45px;
        height: 45px;
        top: 25px;
        margin-left: 10px;
        margin-right: 10px;
    }

    #info {
        height: 30px;
        text-align: left;
        position: relative;
        top: 5px;
        left: 15px;
    }

    #message {
        background-color: cornsilk;
        border: 2px solid rgb(48, 224, 224);
        height: 50px;
        border-radius: 10px;
        font-size: 30px;
        padding-left: 20px;
        padding-right: 20px;
    }
</style>